___CSS_LOADER_EXPORT___.push([module.id, ".modal-overlay{display:flex;align-items:center;justify-content:center;position:fixed;z-index:5000;top:0;left:0;width:100%;height:100%;background:rgba(0,0,0,.45)}.modal-window{z-index:10000;margin:1.5em auto 0;border:2px solid #aaa;background:#fff;max-height:90%;position:relative;overflow-y:auto}.modal-content,.modal-window{padding:10px 20px}.modal-enter-active,.modal-leave-active{transition:opacity .4s}.modal-leave-active{transition:opacity .6s ease .4s}.modal-enter,.modal-leave-to{opacity:0}.bottom-icon{outline:none;display:inline-block;padding:0;text-decoration:none;color:#fff;border-radius:3px}.bottom-icon:active{transform:translateY(4px);border-bottom:none}.save-btn{background:#6f9;border-bottom:4px solid #6c6}.save-btn:active{box-shadow:0 0 1px rgba(0,0,0,.2)}.delete-btn{color:#000;background:#fff;border-bottom:4px solid grey}.delete-btn:active{box-shadow:0 0 1px grey}", "",{"version":3,"sources":["webpack://./resources/js/components/parts/modal_calendar.vue"],"names":[],"mappings":"AA+HA,eACA,YAAA,CACA,kBAAA,CACA,sBAAA,CACA,cAAA,CACA,YAAA,CACA,KAAA,CACA,MAAA,CACA,UAAA,CACA,WAAA,CACA,0BACA,CAEA,cACA,aAAA,CACA,mBAAA,CAEA,qBAAA,CACA,eAAA,CACA,cAAA,CACA,iBAAA,CACA,eACA,CAEA,6BARA,iBAUA,CAGA,wCACA,sBACA,CAGA,oBACA,+BACA,CAEA,6BACA,SACA,CAEA,aACA,YAAA,CACA,oBAAA,CACA,SAAA,CACA,oBAAA,CACA,UAAA,CACA,iBACA,CAEA,oBAGA,yBAAA,CACA,kBACA,CAEA,UACA,eAAA,CACA,4BACA,CAEA,iBACA,iCACA,CAEA,YACA,UAAA,CACA,eAAA,CACA,4BACA,CAEA,mBACA,uBACA","sourcesContent":["<template>\n  <transition name=\"modal\" appear >\n    <div class=\"modal modal-overlay\" @click.self=\"$emit('close')\">\n      <div class=\"modal-window\">\n        <div class=\"mx-auto mb-2\">\n          <div class=\"text-center mb-2\">\n            \n            <select v-model=\"selectedPeriod\">\n              <option disabled value=null></option>\n              <option v-for=\"period in periods\" :value=\"period.period_id\" >{{ periodName(period) }}</option>\n            </select>\n          </div>\n          <div class=\"text-center mb-2\">\n            \n            <select v-model=\"selectedTask\" >\n              <option disabled value=\"\"></option>\n              <option v-for=\"task in periods_tasks\" :value=\"task.task_id\">{{ task.name }}</option>\n            </select>\n          </div>\n\n          <div class=\"row\">\n            <input class=\"col\" type=\"text\" v-model=\"item.title\">\n            <span class=\"mr-2\"></span>\n            <datetime class=\"col\" type=\"datetime\" zone=\"Asia/Tokyo\" v-model=\"item.start\" /> \n            ~\n            <datetime class=\"col\" type=\"datetime\" zone=\"Asia/Tokyo\" v-model=\"item.end\" />\n          </div>\n        </div>\n\n        <!--  -->\n        <div class=\"container\">\n          <div class=\"row justify-content-center\">\n            <button class=\"bottom-icon save-btn col-md-1 col-2\" @click=\"$emit('save', item)\">\n              <span></span>\n            </button>\n\n            <button\n              v-if=\"method === 'update'\"\n              class=\"bottom-icon delete-btn col-md-1 col-2 ml-3\" \n              @click=\"$emit('deleteItem', item)\"\n            >\n              <span></span>\n            </button>\n          </div>\n        </div>\n\n        \n\n      </div>\n    </div>\n  </transition>\n</template>\n\n<script>\nexport default {\n  data(){ \n    return {\n      item: {},\n      tasks: [],\n      periods: {},\n    };\n  },\n  props:{\n    method: String,\n    selected_time: Object,\n    selected_item: Object,\n  },\n  created(){\n    this.init();\n\n    if(this.method === 'insert'){\n      this.makeItem();\n    }else{\n      this.item = { ...this.selected_item};\n    }\n  },\n  computed: {\n    selectedPeriod: {\n      get () { return this.$store.state.period.selected_period },\n      set (val) { this.$store.commit('period/setSelectedPeriod', val) },\n    },\n    periodName(){\n      return(period) => {\n        const start = new Date(period.start).toLocaleDateString();\n        const end = new Date(period.end).toLocaleDateString();\n        const date = start + \"~\" + end;\n        return date;\n      };\n    },\n    periods_tasks() {\n      return this.tasks.filter(task => task.period_id === this.selectedPeriod);\n    },\n    selectedTask: {\n      get () { return this.item.task_id },\n      set (val) { this.changeValue(val) },\n    },\n  },\n  methods:{\n    init(){\n      axios\n        .get('/api/task/index')\n        .then((response) => {\n          this.tasks = response.data.tasks;\n          this.periods = response.data.periods.periods;\n      });\n    },\n    makeItem(){\n      this.item = {\n        'end' :this.selected_time.end,\n        'title': null,\n        'remark': null,\n        'start': this.selected_time.start,\n        'task_id': null,\n      };\n    },\n    changeValue(column, value){\n      if(column === 'task_id'){\n        this.$set(this.item, column, Number(value));\n        const task = this.periods_tasks.find((task) => task.task_id === Number(value));\n        this.$set(this.item, 'title', task.name);\n      }\n    },\n  },\n}\n</script>\n\n<style>\n.modal-overlay {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  position: fixed;\n  z-index: 5000;\n  top: 0;\n  left: 0;\n  width: 100%;\n  height: 100%;\n  background: rgba(0, 0, 0, 0.45);\n}\n\n.modal-window {\n  z-index: 10000;\n  margin: 1.5em auto 0;\n  padding: 10px 20px;\n  border: 2px solid #aaa;\n  background: #fff;\n  max-height: 90%;\n  position: relative;\n  overflow-y: auto;\n}\n\n.modal-content {\n  padding: 10px 20px;\n}\n\n/*  */\n.modal-enter-active, .modal-leave-active {\n  transition: opacity 0.4s;\n}\n\n/*  */\n.modal-leave-active {\n  transition: opacity 0.6s ease 0.4s;\n}\n\n.modal-enter, .modal-leave-to {\n  opacity: 0;\n}\n\n.bottom-icon {\n  outline:none;\n  display: inline-block;\n  padding: 0px;\n  text-decoration: none;\n  color: #FFF;\n  border-radius: 3px;\n}\n\n.bottom-icon:active{\n  /**/\n  -webkit-transform: translateY(4px);\n  transform: translateY(4px);/**/\n  border-bottom: none;\n}\n\n.save-btn {\n  background: #66FF99;/**/\n  border-bottom: solid 4px #66CC66;\n}\n\n.save-btn:active {\n  box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.2);/**/\n}\n\n.delete-btn{\n  color: black;\n  background: white;/**/\n  border-bottom: solid 4px gray;\n}\n\n.delete-btn:active{\n  box-shadow: 0px 0px 1px gray;/**/\n}\n\n</style>"],"sourceRoot":""}]);
